
.module-form {
  -webkit-box-shadow: 0 0 8px 2px #e4e4e6;
          box-shadow: 0 0 8px 2px #e4e4e6;
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
}
.module-form-title {
  margin-bottom: 22px;
}
.module-form-feeling {
  padding-bottom: 20px;
  border-bottom: 1px solid #dfdfdf;
}
.module-form-title-text {
  font-size: 20px;
  font-family: "Microsoft yahei";
  font-weight: 700;
  color: #5f5f5f;
}
.feelings {
  text-align: center;
  color: #9b9b9b;
}
.feelings li {
    float: left;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    margin-left: 26px;
    cursor: pointer;
}
.feelings li:first-child {
    margin-left: 0;
}
.feelings img {
    width: 100%;
    margin-bottom: 4px;
}
.module-form-box {
  margin-top: 20px;
}
.module-form-box-to {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.active-job {
    background-color: #c9c9c9;
}
.module-form-box-to-d1 {
    padding-bottom: 4px;
}
.module-form-box-to-d2 span {
    display: inline-block;
    padding: 4px 16px;
    border: 1px solid #c9c9c9;
    border-radius: 20px;
    margin-left: 8px;
    margin-bottom: 4px;
    cursor: pointer;
}
.module-form-box-to-d2 span:hover {
    background-color: #c9c9c9;
}
.module-form-box input, .module-form-box textarea {
  width: 100%;
  border: 1px solid #dfdfdf;
  padding: 12px;
  font-size: 18px;
  border-radius: 8px;
  outline: none;
}
.module-form-box-title {
  margin-top: 18px;
}
.module-form-box-main {
  margin-top: 12px;
}
.module-form-box-main textarea {
    resize: none;
    height: 244px;
}
.module-form-button {
  margin-top: 20px;
}
.module-form-button button {
    padding: 10px 84px;
    color: #fff;
    font-family: "Microsoft yahei";
    background-color: #1cba5b;
    border: none;
    font-size: 18px;
    outline: none;
    cursor: pointer;
}



